<template>
  <article>
    <h1
      class="font-nunito font-bold text-5xl animate__animated animate__slow"
      :class="[scrollY >= rect ? 'animate__fadeInLeft' : '']"
    >
     <span class="bg-clip-text text-transparent bg-gradient-to-r from-green-400 to-blue-500">Experience</span> 
    </h1>
    <div class="flex flex-col" v-for="item in myEducation" :key="item">
      <!-- component -->

      <div class="flex flex-row relative font-prompt dark:text-gray-400">
        <div class="w-1/2 text-right">
          <ul class="list-none m-0 p-0">
            <li class="mb-2">
              <div class="flex items-center mb-1">
                <div class="flex-1 ml-12 text-2xl mr-12">
                  {{ item.branch }}
                </div>
                <div class="w-12 h-12 z-10 -mx-6">
                  <img :src="icons" />
                </div>
              </div>
              <div class="mr-12">
                {{ item.name }}
              </div>
            </li>
          </ul>
          <div
            class="border-r-2 border-gray-500 border-dotted h-full absolute z-0 top-0 right-1/2"
          ></div>
        </div>

        <div class="flex-1 ml-12 text-left mt-2 text-2xl">
          {{ item.year }}
        </div>
      </div>
    </div>
  </article>
</template>

<script>
import { ref } from "vue";
import { useStore } from "vuex";
export default {
  name: "Education",
  props: { rect: Number, scrollY: Number },
  setup() {
    const store = useStore();
    const icons = ref(
      "data:image/svg+xml;base64,"
    );
    const {
      state: { myEducation },
    } = store;
    return { myEducation, icons };
  },
};
</script>
